
.container{
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  padding: 20px 110px;
  height: auto;
  /* justify-content: center;
  align-items: center; */
}
.reddit-meme-card {
  display: flex;
  border: 1px solid #ddd;
  border-radius: 8px;
  margin:10px 0;
  width: 32%;
  background-color: white;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s;
}

.reddit-meme-card:hover {
  transform: translateY(-5px);
}

.vote-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  background-color: #f7f7f7;
}

.vote-section button {
  background: none;
  border: none;
  cursor: pointer;
  color: #888;
  font-size: 20px;
  margin: 5px 0;
  transition: color 0.2s;
}

.vote-section button:hover {
  color: #007bff;
}

.vote-section span {
  font-size: 16px;
  font-weight: bold;
  color: #333;
}

.content-section {
  padding: 15px;
  flex-grow: 1;
}

.title {
  height: 60px;
  font-size: 18px;
  font-weight: bold;
  color: #333;
  margin: 0 0 10px;
}

.image {
  width: 100%;
  height: 300px;
  border-radius: 4px;
  margin-bottom: 10px;
}

.description {
  font-size: 14px;
  color: #666;
  margin: 0 0 10px;
}

.comments-section {
  display: flex;
  align-items: center;
  font-size: 14px;
  color: #888;
}

.comments-section svg {
  margin-right: 5px;
}

@media  screen and (max-width: 1280px) {

  .container{
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    padding: 20px 110px;  
  }
  .reddit-meme-card {
    width: 40%;  
  }
  
}
@media  screen and (max-width: 1024px) {
  .container{
    padding: 20px 55px;
  }

  .reddit-meme-card {
    width: 40%;  
  }
  
}

@media  screen and (max-width: 768px) {
  .container{
    padding: 20px 85px;
  }

  .reddit-meme-card {
    width: 100%;  
  }
  .title{
    height: 85px;
  }
  
}
@media  screen and (max-width: 640px) {
  .container{
    padding: 20px 80px;
     justify-content: center;
  align-items: center;
  }

  .reddit-meme-card {
    width: 100%;  
  }
  .title{
    height: 85px;
  }
  
}

@media screen and (max-width: 468px) {
  .container{
    padding: 20px 40px;
     justify-content: center;
  align-items: center;

  }
  .title{
    height: 60px;
    font-size: 12px;
  }
  .image {
    width: 100%;
    height: auto;
  }

  
}

